import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, startWith, tap } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { BookingService } from 'app/shared/services/booking/booking.service';
import { ClientService } from 'app/shared/services/client/client.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';

export class ChargingStation {
  id: number;
  station_name: string;
  cpo_name: string;
  code: string;
  address1: string;
  PIN: number;
  description: string;
  city_name: string;
  state_name: string;
  lat: number;
  lng: number;
  mobile: number;
  email: string;
  created_date: string;
  commissioned_dt: string;
  o_time: string;
  c_time: string;
  createdby: string;
  status: string;
  statusString: string;

}


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}


@Component({
  selector: 'app-action-dialog',
  templateUrl: './action-dialog.component.html',
  styleUrls: ['./action-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ActionDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  activeChar_station: ChargingStation[];
  booking_config: any
  selectedList = [];
  clientId: number;
  cpoId: number;
  status = false;
  chargingStation: any
  charStation:any
  filteredOptionsCharClient: Observable<any[]>;
  filteredOptionsCharCPO: Observable<any[]>;
  filteredOptionsCharStat: Observable<any[]>;
  cposName: any;
  clientName: any;
  @ViewChild("activeElement") active: any;
  menuArray: any;

  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, public dialogRef: MatDialogRef<ActionDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private router: Router, private bookingConfig: BookingService,
    private clientService: ClientService, private cpoService: CpoService,) {


  }

  ngOnInit() {
    this.initialize();

  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
    this.params = this.data;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      charClient: ['', [autocompleteObjectValidator()]],
      charCPO: ['', [autocompleteObjectValidator()]],
      char_station: ['',],
      buff_time: ['', [Validators.required, Validators.pattern(/^[0-9 ]*$/)]],
      bookingValid_time: ['', [Validators.required]],
      minChar_Durn: ['', [Validators.required]],
      maxChar_Durn: ['', [Validators.required]],
      bookingCanl_time: ['', [Validators.required, Validators.pattern(/^[0-9 ]*$/)]],
      advBook_amtTime: ['', [Validators.required]],
      advBook_amt: ['', [Validators.required]],
      status: this.status == true ? 'Y' : 'N'
    });

    
    if (this.isEditMode) {
      this.assignValues(this.params);
      this.getClients();
      this.getCPOByClient(this.params.data.client_id);
      this.clientId = this.params.data.client_id;
      this.cpoId = this.params.data.cpo_id;
      this.getAllActiveChargingStation();
    }
  }
  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }
  onYesClick() {
    if (this.form.valid) {
      let formValues = this.form.value;
     
      // for(var i = 0; i < formValues.char_station.length; i++){
      //   var stationId = formValues.char_station[i].id;
      //   this.selectedList.push(stationId);
      // }
      const params: any = {
        stations: formValues.char_station.map(charSta =>({station_id:charSta.id,id:this.params.data.id })),//this.params.data.station_id, //formValues.rfidlist.map(v => ({ id: v.rfid_id })),
        buffer_time: formValues.buff_time,
        booking_validity_time: formValues.bookingValid_time,
        min_charging_duration: formValues.minChar_Durn,
        max_charging_duration: formValues.maxChar_Durn,
        booking_cancellation_time: formValues.bookingCanl_time,
        adv_booking_time: formValues.advBook_amtTime,
        adv_booking_amount: formValues.advBook_amt,
        status: this.status == true ? 'Y' : 'N'
      }
      if (this.isEditMode) {
          params.id = this.params.data.id,
          params.modify_by = this.localStorage.userId;
      } else {
          params.created_by = this.localStorage.userId;
      }

      if (this.isEditMode) {
        this.bookingConfig.updateBookingConfig(params).subscribe(
          (resp: any) => {
            if (resp.status == true) {
              this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
              this.dialogRef.close();
            } else {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
            }
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      } else {
        this.bookingConfig.createBookingConfig(params).subscribe(
          (resp: any) => {
            this.snackBar.open(MessageConstant.bookingConfigCreate, 'Dismiss', { duration: 5000 });
            this.dialogRef.close();
          },
          (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }
        )
      }
      this.dialogRef.close(params);
    } else {
      this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
    }
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    this.form.get('buff_time').setValue(params.data.buffer_time);
    this.form.get('bookingValid_time').setValue(this.params.data.booking_validity_time);
    this.form.get('minChar_Durn').setValue(params.data.min_charging_duration);
    this.form.get('maxChar_Durn').setValue(params.data.max_charging_duration);
    this.form.get('bookingCanl_time').setValue(params.data.booking_cancellation_time);
    this.form.get('advBook_amtTime').setValue(params.data.adv_booking_time);
    this.form.get('advBook_amt').setValue(params.data.adv_booking_amount);
    //this.form.get('char_station').setValue(params.data.station_name);
    this.status = this.params.data.status == 'Y' ? true : false;
  }
  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }

  getAllActiveChargingStation() {
    this.cpoId = null || undefined ? 0 : this.cpoId;  
    this.bookingConfig.getAllActiveChargingStations(this.clientId, this.cpoId).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.activeChar_station = resp.data;
       
          var selectedListStation = [];
            for (let i = 0; i < this.activeChar_station.length; i++) {
              var station = this.activeChar_station[i];
              if(station.id != null && station.id != undefined){
                if(this.params.data.station_id == station.id){
                  selectedListStation.push(station);
                }
                
              }
          }
          this.form.get('char_station').setValue(selectedListStation);
       

        } else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }
      },
      (err) => { }
    );

  }

  getClients() {
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (resp: any) => {
        if (resp != null) {
          this.clientName = resp;
          if(this.isEditMode){
            //;
            const toSelect = this.clientName.find(c => c.id == this.params.data.client_id);
            this.form.get('charClient').setValue(toSelect);
          }
          this.filteredOptionsCharClient = this.form.controls.charClient.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharClient(name) : this.clientName)
            );
        }
      }, (err) => {

      }
    )
  }
  private _filterCharClient(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clientName.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayCharClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  onClientChange(client_id) {
    this.activeChar_station = [];
    this.cpoId=0;
    this.clientId = client_id;
    this.form.get('charCPO').setValue("");
    this.getCPOByClient(client_id);
    this.getAllActiveChargingStation();
    
  }

  getCPOByClient(client_id) {
    this.cpoService.getCpoByClientId(client_id).subscribe(
      (resp: any) => {
        if (resp != null) {
          this.cposName = resp;
          if(this.isEditMode){
            //;
            const toSelect = this.cposName.find(c => c.id == this.params.data.cpo_id);
            this.form.get('charCPO').setValue(toSelect);
          }
          this.filteredOptionsCharCPO = this.form.controls.charCPO.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharCPO(name) : this.cposName)
            );
        }

      }, (err) => { }
    )
  }
  private _filterCharCPO(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cposName.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayCharCPO(item: any): string {
    return item && item.name ? item.name : '';
  }
  onCPOChange(cpo_id) {
    this.activeChar_station = [];
    this.cpoId = cpo_id;
    this.getAllActiveChargingStation();

  }

  filteredOptions() {
    this.filteredOptionsCharClient = this.form.controls.charClient.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharClient(name) : this.clientName)
      );

    this.filteredOptionsCharCPO = this.form.controls.charCPO.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharCPO(name) : this.cposName)
      );
  }





}










