
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import * as XLSX from 'xlsx';
import { BookingService } from 'app/shared/services/booking/booking.service';


export class BookingStationConfig {
  id: number;
  station_id: number;
  station_name:string;
  cpo_name:string;
  client_name:string;
  buffer_time: number;
  booking_validity_time: number;
  min_charging_duration: number;
  max_charging_duration: number;
  booking_cancellation_time: number;
  adv_booking_time: number;
  adv_booking_amount: number;
  address1:string;
  status: string;
  statusString: string;

}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ListComponent implements OnInit {


  filterEntity: BookingStationConfig;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['station_name','cpo_name', 'buffer_time', 'booking_validity_time','client_name', 'min_charging_duration', 
        'adv_booking_amount', 'status', 'action'];
  head = [['Station Id', 'Buffer Time', 'Booking Validity Time', 'Min Charging Duration', 'Adv Booking Time', 'Adv Booking Amount', 'Max Charging Duration',
    , 'Status']]
  dataSource: MatTableDataSource<BookingStationConfig>;
  bookingConfigData: BookingStationConfig[];
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  dialogRef: any;
  cpoSearch = false;
  stationNameSearch = false;
  bufferTimeSearch = false;
  bookvalTimeSearch = false;
  minCharDurSearch = false;
  disp_ordSearch = false;
  advBookTimeSearch = false;
  advBookAmtSearch = false;
  clientNameSearch = false;
  //nav_idSearch = false;
  statusSearch = false;
  exportedData: any[];
  localStorage: any;
  //messageConstant = MessageConstant;
  constructor(public dialog: MatDialog, private bookingConfig: BookingService, private snackBar: MatSnackBar, private cdr: ChangeDetectorRef) {

  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.getBookingConfig();
    this.filterEntity = new BookingStationConfig();
    this.filterType = MatTableFilter.ANYWHERE;

  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getBookingConfig() {
    this.bookingConfig.getAllBookingConfigList().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.bookingConfigData = resp.data;
          for (var i = 0; i < this.bookingConfigData.length; i++) {
            if (this.bookingConfigData[i].status == 'N') {
              this.bookingConfigData[i].statusString = 'Inactive';
            } else {
              this.bookingConfigData[i].statusString = 'Active';
            }
          }
          this.dataSource = new MatTableDataSource(this.bookingConfigData);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;

        } else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }

      },
      (err) => { }
    );
  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });
    this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.bookingConfig.deleteBookingConfig(id, this.localStorage.userId).subscribe(
          (resp: any) => {
            if (resp.status == true) {
           
              this.getBookingConfig();
              this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 3000 });
              this.cdr.detectChanges();
            } else {
              this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
            }
          }, (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
          }
        )
      }
      this.confirmDialogRef = null;
    });

  }

  viewDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1400px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getBookingConfig();
    });
  }
  openDialog(action, data): void {
    // this.snackBar.open('Unable to proceed, In prgress','Dismiss');
    // return false;
    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1400px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getBookingConfig();
      this.cdr.detectChanges();
    });
  }

  changeVisibility(visibleOption, ev) {

    ev.stopPropagation();
    if (visibleOption == 'cpoSearch') {
      this.cpoSearch = !this.cpoSearch;
      this.filterEntity.cpo_name = '';
    }
    if (visibleOption == 'clientNameSearch') {
      this.clientNameSearch = !this.clientNameSearch;
      this.filterEntity.client_name = '';
    }
    if (visibleOption == 'stationNameSearch') {
      this.stationNameSearch = !this.stationNameSearch;
      this.filterEntity.station_name = '';
    }
    if (visibleOption == 'bufferTimeSearch') {
      this.bufferTimeSearch = !this.bufferTimeSearch;
      this.filterEntity.buffer_time = null;
    }
    if (visibleOption == 'bookvalTimeSearch') {
      this.bookvalTimeSearch = !this.bookvalTimeSearch;
      this.filterEntity.booking_validity_time = null;
    }
    if (visibleOption == 'minCharDurSearch') {
      this.minCharDurSearch = !this.minCharDurSearch;
      this.filterEntity.min_charging_duration = null;
    }
    if (visibleOption == 'advBookTimeSearch') {
      this.advBookTimeSearch = !this.advBookTimeSearch;
      this.filterEntity.adv_booking_time = null;
    }
    if (visibleOption == 'advBookAmtSearch') {
      this.advBookAmtSearch = !this.advBookAmtSearch;
      this.filterEntity.adv_booking_amount = null;
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }

  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var bookingconfig = this.dataSource.filteredData[i];
      var data = {
        'Station Id': bookingconfig.station_id, 'Buffer Time': bookingconfig.buffer_time, 'Booking Validity Time': bookingconfig.booking_validity_time, 'Min Charging Duration': bookingconfig.min_charging_duration,
        'Adv Booking Time': bookingconfig.adv_booking_time, 'Adv Booking Amount ': bookingconfig.adv_booking_amount, 'Max Charging Duration': bookingconfig.max_charging_duration,
        'Status': bookingconfig.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
    XLSX.writeFile(workBook, 'Booking Station Config.xlsx');
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var bookingconfig = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [bookingconfig.station_id, bookingconfig.buffer_time, bookingconfig.booking_validity_time, bookingconfig.min_charging_duration, bookingconfig.adv_booking_time,
      bookingconfig.adv_booking_amount, bookingconfig.max_charging_duration, bookingconfig.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Booking Station Config Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not For Public Use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')

    // below line for Download PDF document  
    doc.save('Booking Station Config report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example
    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right");
    return doc;

  }



}

