
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BookingConfigCrudComponent } from './booking-config-crud/booking-config-crud.component';
import { TABS_DEMO_ROUTES } from './booking-config-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : BookingConfigCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class BookingConfigRoutingModule{}