import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl, FormArray, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { PATTERNS } from 'app/shared/utils/validation-patterns';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MenuMasterService } from 'app/shared/services/menu-master/menu-master.service';
import { ClientService } from 'app/shared/services/client/client.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { MatTableFilter } from 'mat-table-filter';
import { BookingService } from 'app/shared/services/booking/booking.service';
import { SelectionModel } from '@angular/cdk/collections';

export class ChargingStation {
  id: number;
  //station_id: number;
  station_name: string;
  cpo_name: string;
  code: string;
  address1: string;
  PIN: number;
  description: string;
  city_name: string;
  state_name: string;
  lat: number;
  lng: number;
  mobile: number;
  email: string;
  created_date: string;
  commissioned_dt: string;
  o_time: string;
  c_time: string;
  createdby: string;
  status: string;
  statusString: string;

}

function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})


export class CreateComponent implements OnInit {

  filterEntity: ChargingStation;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['select', 'id', 'station_name', 'cpo_name', 'city_name', 'address1', 'description', 'PIN', 'status'];
  selection = new SelectionModel<ChargingStation>(true, []);
  dataSource: MatTableDataSource<ChargingStation>;
  activeChar_station: ChargingStation[];
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  idSearch = false;
  staNameSearch = false;
  typeSearch = false;
  copNameSearch = false;
  cityNameSearch = false;
  address1Search = false;
  descriptionSearch = false;
  pinSearch = false;
  statusSearch = false;
  showTable = false;
  chargingStation: any
  selectedList = [];
  clientId: number;
  cpoId: number;

  form: FormGroup;
  listform: FormGroup;
  localStorage: any;
  status = true;
  filteredOptionsCharClient: Observable<any[]>;
  filteredOptionsCharCPO: Observable<any[]>;
  filteredOptionsCharStat: Observable<any[]>;
  cposName: any;
  clientName: any;

  @ViewChild("activeElement") active: any;


  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private router: Router, private menuMasterService: MenuMasterService,
    private clientService: ClientService, private cpoService: CpoService, private chargingStationService: ChargingStationService,
    private bookingConfig: BookingService) {

  }

  ngOnInit() {
    this.initialize();
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      buff_time: ['', [Validators.required, Validators.pattern(/^[0-9 ]*$/)]],
      bookingValid_time: ['', [Validators.required]],
      minChar_Durn: ['', [Validators.required]],
      maxChar_Durn: ['', [Validators.required]],
      bookingCanl_time: ['', [Validators.required, Validators.pattern(/^[0-9 ]*$/)]],
      advBook_amtTime: ['', [Validators.required]],
      advBook_amt: ['', [Validators.required]],
      status: this.status == true ? 'Y' : 'N'
    });

    this.listform = this.fb.group({
      charClient: ['', [autocompleteObjectValidator()]],
      charCPO: ['', [autocompleteObjectValidator()]],
      charStat: ['', [autocompleteObjectValidator()]]
    });

    this.filterEntity = new ChargingStation();
    this.filterType = MatTableFilter.ANYWHERE;

    this.getClients();
    //this.getCPOByClient();
    //this.getMenuMaster();

  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  getAllActiveChargingStation() {
    this.cpoId = null || undefined ? 0 : this.cpoId; 
    this.bookingConfig.getAllActiveChargingStations(this.clientId, this.cpoId).subscribe(
          (resp: any) => {
            if (resp.status == true) {
              this.activeChar_station = resp.data;
              for (var i = 0; i < this.activeChar_station.length; i++) {
                if (this.activeChar_station[i].status == 'N') {
                  this.activeChar_station[i].statusString = 'Inactive';
                } else {
                  this.activeChar_station[i].statusString = 'Active';
                }
              }
              this.dataSource = new MatTableDataSource(this.activeChar_station);
              this.dataSource.paginator = this.paginator;
              this.dataSource.sort = this.sort;
              this.showTable = true;
  
            } else {
              this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
              return;
            }
  
          },
          (err) => { }
        );
  }
  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;

    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }
  changeVisibility(visibleOption, ev) {

    ev.stopPropagation();
    if (visibleOption == 'idSearch') {
      this.idSearch = !this.idSearch;
      this.filterEntity.id = null;
    }
    if (visibleOption == 'staNameSearch') {
      this.staNameSearch = !this.staNameSearch;
      this.filterEntity.state_name = '';
    }
    if (visibleOption == 'copNameSearch') {
      this.copNameSearch = !this.copNameSearch;
      this.filterEntity.cpo_name = '';
    }
    if (visibleOption == 'cityNameSearch') {
      this.cityNameSearch = !this.cityNameSearch;
      this.filterEntity.city_name = '';
    }
    if (visibleOption == 'address1Search') {
      this.address1Search = !this.address1Search;
      this.filterEntity.address1 = '';
    }
    if (visibleOption == 'descriptionSearch') {
      this.descriptionSearch = !this.descriptionSearch;
      this.filterEntity.description = '';
    }
    if (visibleOption == 'pinSearch') {
      this.pinSearch = !this.pinSearch;
      this.filterEntity.PIN = null;
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }

  }


  onSubmit() {
    this.chargingStation = this.selection.selected//.map(v => ([v.station_id])).join(", ");
    this.selectedList = [];
    for (var i = 0; i < this.chargingStation.length; i++) {
      var stationId = this.chargingStation[i].id;
      this.selectedList.push(stationId);
    }
    let formValues = this.form.value;

    if (this.form.valid) {
      let params = {
        stations: this.selectedList,
        buffer_time: formValues.buff_time,
        booking_validity_time: formValues.bookingValid_time,
        min_charging_duration: formValues.minChar_Durn,
        max_charging_duration: formValues.maxChar_Durn,
        booking_cancellation_time: formValues.bookingCanl_time,
        adv_booking_time: formValues.advBook_amtTime,
        adv_booking_amount: formValues.advBook_amt,
        status: this.status == true ? 'Y' : 'N',
        created_by: this.localStorage.userId,
      }

      this.bookingConfig.createBookingConfig(params).subscribe(
        (resp: any) => {
          if (resp.status == true) {
            
                if(resp.data[0].statioin_mapped != null){
                  this.snackBar.open(MessageConstant.bookingConfigCreate, 'Dismiss', { duration: 5000 });
                  this.router.navigate(['/bookingManagement/bookingStationConfig/crud/list']);
                }else 
                    if(resp.data[0].station_not_mapped != null){
                          this.snackBar.open(MessageConstant.bookingConfigAlReadyMapped, 'Dismiss', { duration: 5000 });
                          this.router.navigate(['/bookingManagement/bookingStationConfig/crud/create']);
                      }
              else{
                this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
              }                   
          }else{
            this.snackBar.open(MessageConstant.bookingConfigAlReadyMapped, 'Dismiss', { duration: 5000 });
           }      

        }, (err) => {
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      );
    } else {
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 5000 });
    }

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }
  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }

  getClients() {
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (resp: any) => {
        if (resp != null) {
          this.clientName = resp;
          this.filteredOptionsCharClient = this.listform.controls.charClient.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharClient(name) : this.clientName)
            );
        }
      }, (err) => {

      }
    )
  }
  private _filterCharClient(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clientName.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayCharClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  onClientChange(client_id) {
    this.activeChar_station = [];
    this.dataSource = null
    this.cpoId =0;
    this.clientId = client_id;
    this.getAllActiveChargingStation();
    this.listform.get('charCPO').setValue("");
    this.getCPOByClient(client_id);

  }

  getCPOByClient(client_id) {
    this.cpoService.getCpoByClientId(client_id).subscribe(
      (resp: any) => {
        if (resp != null) {
          this.cposName = resp;
          this.filteredOptionsCharCPO = this.listform.controls.charCPO.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCharCPO(name) : this.cposName)
            );
        }

      }, (err) => { }
    )
  }
  private _filterCharCPO(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cposName.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayCharCPO(item: any): string {
    return item && item.name ? item.name : '';
  }
  onCPOChange(cpo_id) {
    this.activeChar_station = [];
    this.dataSource = null;
    this.showTable = false;
    this.cpoId = cpo_id;
    this.getAllActiveChargingStation();

  }

  filteredOptions() {
    this.filteredOptionsCharClient = this.listform.controls.charClient.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharClient(name) : this.clientName)
      );

    this.filteredOptionsCharCPO = this.listform.controls.charCPO.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCharCPO(name) : this.cposName)
      );
  }




}


